@import url(https://fonts.googleapis.com/css?family=PT+Sans:400,700&subset=latin-ext);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

.container {
  max-width: 1200px;
  width: 100%; }

body {
  margin: 0 auto;
  background-color: #EAE7DC;
  background-image: url(/static/media/background2.b9e7c1ae.png);
  font-family: PT Sans, 'sans-serif'; }

.start {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center; }
  .start input {
    width: 500px;
    height: 30px;
    margin-top: 10px;
    margin-bottom: 15px;
    padding: 5px;
    border: 1px solid #8E8D8A; }
    .start input:focus {
      outline: none;
      border: 1px solid #E85A4F; }
  .start label {
    color: #8E8D8A;
    font-weight: bold; }

.container {
  width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center; }

h1 {
  font-size: 55px;
  margin: 0;
  text-transform: uppercase;
  color: #E85A4F;
  text-align: center; }

h2 {
  font-size: 40px;
  font-weight: normal;
  color: #8E8D8A;
  margin: 15px 0; }

button {
  width: 500px;
  height: 40px;
  margin-top: 50px;
  border: 1px solid #A13E37;
  background-color: #E85A4F;
  color: white;
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 700;
  -webkit-transition: 0.4s;
  transition: 0.4s; }
  button:hover {
    -webkit-transform: scale(1.02);
            transform: scale(1.02); }

.data {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center; }
  .data input {
    margin: 10px; }
  .data select {
    width: 500px;
    height: 30px; }
  .data fieldset {
    margin: 20px 0; }

.usage ul {
  list-style: none; }
  .usage ul li {
    padding: 5px 0;
    font-size: 20px;
    font-weight: 700;
    color: #1d1d1d;
    text-align: center; }

.usage fieldset {
  padding: 10px;
  margin: 20px 0;
  border: 2px solid #E85A4F; }

.usage p {
  font-size: 18px;
  color: #8E8D8A; }

.tips {
  margin: 70px 0;
  width: 1000px; }
  .tips h1 {
    margin-bottom: 20px; }
  .tips h3 {
    color: #E85A4F;
    font-size: 25px; }
  .tips p {
    line-height: 32px; }
  .tips a {
    color: #E85A4F;
    font-weight: 700; }

.spending h1 {
  font-size: 40px; }

.check {
  font-weight: 700; }

